
<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 md12>
        <h2 :style="atualColor==='white' ? auxTextColor2 : auxTextColor1">Redes Sociais</h2>
      </v-flex>
    </v-layout>
    <div v-responsive="['hidden-all','md','lg', 'xl']" style="width:100%;">
      <v-row>
        <v-col
          cols="3"
          v-for="(socialNetwork,i) in socialNetworks"
          :key="i"
          class="d-flex justify-center"
        >
          <v-btn icon :href="socialNetwork.href">
            <v-icon
              size="40px"
              :color="atualColor==='white' ? '#111A46' : 'white'"
            >{{ socialNetwork.icon }}</v-icon>
            <span
              class="ml-3"
              :style="atualColor==='white' ? auxTextColor2 + 'font-size: 0.9em;' : auxTextColor1 + 'font-size: 0.95em;'"
            >{{socialNetwork.nick}}</span>
          </v-btn>
        </v-col>
        <v-col cols="12">
          <v-row
            class="d-flex justify-center"
            :style="atualColor==='white' ? auxTextColor2 : auxTextColor1"
          >
            {{ new Date(Date.now()).toDateString() }}  {{ hour }} 
            <strong>Vuetify</strong>
          </v-row>
        </v-col>
      </v-row>
    </div>

    <div v-responsive.sm.xs>
      <v-row>
        <v-col cols="12" v-for="(socialNetwork,i) in socialNetworks" :key="i">
          <v-btn icon :href="socialNetwork.href">
            <v-icon
              size="40px"
              :color="atualColor==='white' ? '#111A46' : 'white'"
            >{{ socialNetwork.icon }}</v-icon>
          </v-btn>
          <span
            class="ml-2 mb-4"
            :style="atualColor==='white' ? auxTextColor2 : auxTextColor1"
          >{{socialNetwork.nick}}</span>
        </v-col>
      </v-row>
      <v-col cols="12">
        <v-row
          class="d-flex justify-center"
          :style="atualColor==='white' ? auxTextColor2 : auxTextColor1"
        >
          {{ new Date(Date.now()).toDateString() }}  {{ hour }} 
          <strong>Vuetify</strong>
        </v-row>
      </v-col>
    </div>
  </v-container>
</template>

<script>
export default {
  name: "HomeSocialNetworks",
  props: ["atualColor"],
  data: () => ({
    valid: false,
    auxTextColor1: "color: white;",
    auxTextColor2: "color: #111A46;",
    date: "",
    hour: "",
    socialNetworks: [
      {
        icon: "mdi-linkedin",
        nick: "mtheuswilliam",
        href: "https://bit.ly/2PVlAWR",
      },
      {
        icon: "mdi-instagram",
        nick: "matheuswilliamdesign",
        href: "https://bit.ly/2PTWMyj",
      },
      {
        icon: "mdi-gmail",
        nick: "matheuswilliamdesign@gmail.com",
        href: "gmail.com",
      },
      {
        icon: "mdi-whatsapp",
        nick: "(86) 99463-1347",
        href: "https://bit.ly/3h1Zq14",
      },
    ],
  }),
  created() {
    this.getHour();
  },
  watch: {
    hour: function () {},
  },
  methods: {
    getHour() {
      var vm = this;
      setInterval(function () {
        let novaHora = new Date();
        let hora = novaHora.getHours();
        let minuto = novaHora.getMinutes();
        let segundo = novaHora.getSeconds();
        minuto = vm.zero(minuto);
        segundo = vm.zero(segundo);
        vm.hour = hora + ":" + minuto + ":" + segundo;
      }, 1);
    },
    zero(x) {
      if (x < 10) {
        x = "0" + x;
      }
      return x;
    },
  },
};
</script>
