<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 md12>
        <h2 :style="atualColor==='white' ? auxTextColor2 : auxTextColor1">Social Media</h2>
        <hr class="mb-4" style="max-width: 200px;" />
      </v-flex>
    </v-layout>

    <div v-responsive="['hidden-all','md','lg', 'xl']" style="width:100%;">
      <v-layout row wrap justify-space-between>
        <v-flex md4 align-self-center>
          <v-img
            class="ml-1 mr-1"
            max-width="500"
            :aspect-ratio="1"
            :src="require('../assets/socialmedia/img' + parseInt(aux_count*3-2) + '.png')"
          >
            <v-row class="d-flex justify-start align-center" style="height:100%;">
              <v-btn
                icon
                @click="beforePage()"
                x-large
                class="ml-3"
                style="background-color:white;"
              >
                <v-icon
                  x-large
                  :color="atualColor==='#ED4D87'? '#ED4D87' : '#111A46'"
                >mdi-arrow-left-bold-hexagon-outline</v-icon>
              </v-btn>
            </v-row>
          </v-img>
        </v-flex>
        <v-flex md4 align-self-center>
          <v-img
            class="ml-1 mr-1"
            max-width="500"
            :aspect-ratio="1"
            :src="require('../assets/socialmedia/img' + parseInt(aux_count*3-1) + '.png')"
          ></v-img>
        </v-flex>
        <v-flex md4 align-self-center>
          <v-img
            class="ml-1 mr-1"
            max-width="500"
            :aspect-ratio="1"
            :src="require('../assets/socialmedia/img' + parseInt(aux_count*3) + '.png')"
          >
            <v-row class="d-flex justify-end align-center" style="height:100%;">
              <v-btn icon @click="nextPage()" x-large class="mr-3" style="background-color:white;">
                <v-icon
                  x-large
                  :color="atualColor==='#ED4D87'? '#ED4D87' : '#111A46'"
                >mdi-arrow-right-bold-hexagon-outline</v-icon>
              </v-btn>
            </v-row>
          </v-img>
        </v-flex>
      </v-layout>
    </div>

    <div v-responsive.sm.xs>
      <v-layout row justify-space-between>
        <v-flex xs12 align-self-center class="pl-3 pr-3">
          <v-img
            max-width="500"
            :aspect-ratio="1"
            :src="require('../assets/socialmedia/img' + parseInt(aux_image) + '.png')"
          >
            <v-row class="d-flex justify-center align-center" style="height:100%;">
              <v-btn icon @click="beforeImg()" x-large class="ml-3" style="background-color:white;">
                <v-icon
                  x-large
                  :color="atualColor==='#ED4D87'? '#ED4D87' : '#111A46'"
                >mdi-arrow-left-bold-hexagon-outline</v-icon>
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn icon @click="nextImg()" x-large class="mr-3" style="background-color:white;">
                <v-icon
                  x-large
                  :color="atualColor==='#ED4D87'? '#ED4D87' : '#111A46'"
                >mdi-arrow-right-bold-hexagon-outline</v-icon>
              </v-btn>
            </v-row>
          </v-img>
        </v-flex>
      </v-layout>
    </div>
    <v-dialog v-model="dialog" :width="auxWidth">
      <v-img :src="auxSrc"></v-img>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  name: "HomeBranding",
  props: ["atualColor"],
  data: () => ({
    auxTextColor1: "color: white;",
    auxTextColor2: "color: #111A46;",
    auxSrc: "",
    auxWidth: 800,
    dialog: false,
    aux_count: 1,
    aux_image: 1,
  }),
  methods: {
    nextPage() {
      if (this.aux_count === 4) {
        this.aux_count = 1;
      } else {
        this.aux_count = this.aux_count + 1;
      }
    },
    beforePage() {
      if (this.aux_count === 1) {
        this.aux_count = 4;
      } else {
        this.aux_count = this.aux_count - 1;
      }
    },
    nextImg() {
      if (this.aux_image === 12) {
        this.aux_image = 1;
      } else {
        this.aux_image = this.aux_image + 1;
      }
    },
    beforeImg() {
      if (this.aux_image === 1) {
        this.aux_image = 12;
      } else {
        this.aux_image = this.aux_image - 1;
      }
    },
  },
};
</script>
