<template>
  <v-app :style="auxColorBg + atualColor" id="about">
    <v-app-bar :style="auxColorBg + atualColor" elevate-on-scroll fixed class="pb-2" height="85">
      <v-container>
        <v-layout row justify-space-between>
          <v-flex md1 sm4>
            <v-img
              v-if="atualColor === '#111A46'"
              alt="Matheus William Logo"
              class="ml-3"
              contain
              src="@/assets/logo_mw.svg"
              transition="scale-transition"
              width="65"
            />
            <v-img
              v-if="atualColor === '#ED4D87'"
              alt="Matheus William Logo"
              class="ml-3"
              contain
              src="@/assets/logo_pink.svg"
              transition="scale-transition"
              width="65"
            />
            <v-img
              v-if="atualColor === 'white'"
              alt="Matheus William Logo"
              class="ml-3"
              contain
              src="@/assets/logo_white.svg"
              transition="scale-transition"
              width="65"
            />
          </v-flex>
          <v-flex md1 sm4>
            <v-btn icon v-if="atualColor !== '#111A46'" @click="atualColor='#111A46'" class="mt-2">
              <v-icon color="#111A46">mdi-eye</v-icon>
            </v-btn>
            <v-btn icon v-if="atualColor !== '#ED4D87'" @click="atualColor='#ED4D87'" class="mt-2">
              <v-icon color="#ED4D87">mdi-eye</v-icon>
            </v-btn>
            <v-btn icon v-if="atualColor !== 'white'" @click="atualColor='white'" class="mt-2">
              <v-icon color="white">mdi-eye</v-icon>
            </v-btn>
          </v-flex>

          <div v-responsive="['hidden-all','md','lg', 'xl']">
            <v-flex md12>
              <v-row>
                <v-spacer></v-spacer>
                <v-btn
                  class="mt-2"
                  v-for="(item, i) in menu_items"
                  :key="i"
                  text
                  @mouseover="item.color = '#ED4D87'"
                  @mouseout="item.color = 'white'"
                  :color="atualColor==='white'? '#111A46' : item.color"
                  @click="
              $vuetify.goTo(item.src, {
                duration: 1000,
                offset: 0,
                easing: 'easeInOutCubic',
              });
            "
                >
                  <span style="font-size: 0.9em;">{{item.title}}</span>
                </v-btn>
              </v-row>
            </v-flex>
          </div>
          <div v-responsive.sm.xs>
            <v-flex sm4>
              <v-btn
                :color="atualColor==='white'? '#111A46' : 'white'"
                icon
                class="mr-3 mt-1"
                x-large
                @click="drawer=true"
              >
                <v-icon x-large>mdi-view-headline</v-icon>
              </v-btn>
            </v-flex>
          </div>
        </v-layout>
      </v-container>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" app temporary>
      <v-list nav dense>
        <v-list-item-group color="#111A46">
          <v-list-item
            v-for="(item, i) in menu_items"
            :key="i"
            @click="
              $vuetify.goTo(item.src, {
                duration: 1000,
                offset: 0,
                easing: 'easeInOutCubic',
              });drawer= !drawer
            "
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-main>
      <HomeAbout :atualColor="atualColor" class="mt-12 pt-8" />
      <HomeBranding id="branding" :atualColor="atualColor" />
      <HomeSocialMedia id="socialmedia" :atualColor="atualColor" />
      <HomeContact id="contato" :atualColor="atualColor" />
      <HomeSocialNetworks :atualColor="atualColor" />
    </v-main>
  </v-app>
</template>

<script>
import HomeAbout from "./components/HomeAbout";
import HomeBranding from "./components/HomeBranding";
import HomeSocialMedia from "./components/HomeSocialMedia";
import HomeContact from "./components/HomeContact";
import HomeSocialNetworks from "./components/HomeSocialNetworks";

export default {
  name: "App",

  components: {
    HomeAbout,
    HomeBranding,
    HomeSocialMedia,
    HomeContact,
    HomeSocialNetworks
  },

  data: () => ({
    hover: "white",
    atualColor: "#111A46",
    auxColorBg: "background-color:",
    siteColors_items: ["#111A46", "#ED4D87", "white"],
    menu_items: [
      { title: "HOME", color: "white", src: "#about" },
      { title: "SOBRE", color: "white", src: "#about" },
      { title: "BRANDING", color: "white", src: "#branding" },
      { title: "SOCIAL MEDIA", color: "white", src: "#socialmedia" },
      { title: "CONTATO", color: "white", src: "#contato" }
    ],
    drawer: false
  })
};
</script>