<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 md12>
        <h2 :style="atualColor==='white' ? auxTextColor2 : auxTextColor1">Contato</h2>
      </v-flex>
      <div v-responsive="['hidden-all','md','lg', 'xl']" style="width:100%;">
        <v-form
          id="form"
          class="form"
          ref="form"
          v-model="valid"
          lazy-validation
          @submit.prevent="sendEmail"
        >
          <v-layout wrap justify-space-between>
            <v-flex md-12>
              <v-row>
                <v-col cols="3">
                  <v-text-field
                    dark
                    :background-color="atualColor==='white' ? '#111A46' : ''"
                    filled
                    v-model="name"
                    :rules="nameRules"
                    label="Nome"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="3">
                  <v-text-field
                    dark
                    :background-color="atualColor==='white' ? '#111A46' : ''"
                    filled
                    v-model="email"
                    :rules="emailRules"
                    label="E-mail"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="3">
                  <v-text-field
                    dark
                    :background-color="atualColor==='white' ? '#111A46' : ''"
                    filled
                    v-model="numero"
                    label="N칰mero"
                  ></v-text-field>
                </v-col>
                <v-col cols="3">
                  <v-text-field
                    dark
                    :background-color="atualColor==='white' ? '#111A46' : ''"
                    filled
                    v-model="subject"
                    :rules="nameRules"
                    label="Assunto"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" class="pt-0">
                  <v-textarea
                    v-model="content"
                    dark
                    filled
                    :background-color="atualColor==='white' ? '#111A46' : ''"
                    auto-grow
                    label="Conte칰do do email"
                    :rules="contentRules"
                    clearable
                    required
                  ></v-textarea>
                </v-col>
                <v-col cols="12" class="pt-0">
                  <v-row class="pr-3">
                    <v-spacer></v-spacer>
                    <v-btn color="error" @click="reset">Reset</v-btn>
                    <v-btn color="success" @click="validate" class="ml-3">Enviar</v-btn>
                  </v-row>
                </v-col>
              </v-row>
            </v-flex>
          </v-layout>
        </v-form>
      </div>
      <div v-responsive.sm.xs>
        <v-card flat tile :color="atualColor" style="width:100%;" class="pl-3 pr-3">
          <v-form
            id="form"
            class="form"
            ref="form"
            v-model="valid"
            lazy-validation
            @submit.prevent="sendEmail"
          >
            <v-row>
              <v-col cols="12">
                <v-text-field
                  dark
                  :background-color="atualColor==='white' ? '#111A46' : ''"
                  filled
                  v-model="name"
                  :rules="nameRules"
                  label="Nome"
                  required
                ></v-text-field>
              </v-col>

              <v-col cols="12" style="margin-top: -30px;">
                <v-text-field
                  dark
                  :background-color="atualColor==='white' ? '#111A46' : ''"
                  filled
                  v-model="email"
                  :rules="emailRules"
                  label="E-mail"
                  required
                ></v-text-field>
              </v-col>

              <v-col cols="12" style="margin-top: -30px;">
                <v-text-field
                  dark
                  :background-color="atualColor==='white' ? '#111A46' : ''"
                  filled
                  v-model="numero"
                  label="N칰mero"
                ></v-text-field>
              </v-col>

              <v-col cols="12" style="margin-top: -30px;">
                <v-text-field
                  dark
                  :background-color="atualColor==='white' ? '#111A46' : ''"
                  filled
                  v-model="subject"
                  :rules="nameRules"
                  label="Assunto"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <v-textarea
              v-model="content"
              dark
              style="margin-top: -15px;"
              filled
              :background-color="atualColor==='white' ? '#111A46' : ''"
              auto-grow
              label="Conte칰do do email"
              :rules="contentRules"
              clearable
              required
            ></v-textarea>
            <v-row>
              <v-spacer></v-spacer>
              <v-btn color="error" class="mr-4" @click="reset">Reset</v-btn>
              <v-btn color="success" class="mr-4" @click="validate">Enviar</v-btn>
            </v-row>
          </v-form>
        </v-card>
      </div>
    </v-layout>
    <div class="text-center">
      <v-dialog v-model="loading" max-width="400">
        <v-card color="#111A46" dark>
          <v-card-text style="color:white;">
            <v-row class="pt-2 pb-3">
              <br />
              <v-spacer></v-spacer>
              <span style="font-size: 1.3em; color:white;">{{ loadingMessage }}</span>
              <v-spacer></v-spacer>
              <v-btn
                style="margin-bottom: -10px; color: #111A46;"
                class="mt-8"
                v-if="!loadingStatus"
                @click="loading=false; reset();"
                color="white"
              >OK</v-btn>
            </v-row>
            <v-row v-if="loadingStatus">
              <v-spacer></v-spacer>
              <v-progress-circular indeterminate color="white" class="mb-0"></v-progress-circular>
              <v-spacer></v-spacer>
            </v-row>
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
  </v-container>
</template>

<script>
import emailjs from "emailjs-com";
export default {
  name: "HomeContact",
  props: ["atualColor"],
  data: () => ({
    valid: false,
    loading: false,
    loadingStatus: false,
    loadingMessage: "",
    auxTextColor1: "color: white;",
    auxTextColor2: "color: #111A46;",
    name: "",
    nameRules: [
      (v) => !!v || "Voc칡 precisa inserir seu nome",
      (v) => v.length <= 30 || "Seu nome deve possuir menos de 30 caracteres",
    ],
    email: "",
    emailRules: [
      (v) => !!v || "Voc칡 precisa inserir seu e-mail",
      (v) => /.+@.+/.test(v) || "Voc칡 precisa inserir um e-mail v치lido",
    ],
    numero: "",
    subject: "",
    subjectRules: [
      (v) => !!v || "Voc칡 precisa inserir o assunto do email",
      (v) => v.length <= 30 || "O assunto deve possuir menos de 30 caracteres",
    ],
    content: "",
    contentRules: [(v) => !!v || "Voc칡 precisa inserir o conte칰do do email"],
  }),
  methods: {
    sendEmail() {
      var vm = this;
      var templateParams = {
        name: this.name,
        subject: this.subject,
        user_email: this.email,
        numero: this.numero,
        message: this.content,
      };
      emailjs
        .send(
          "matheus_william_design",
          "template_ifHGB71U",
          templateParams,
          "user_AhJTDZhzZRgJiafTgweSf"
        )
        .then(
          function (response) {
            vm.loadingStatus = false;
            vm.loadingMessage =
              "Seu e-mail foi enviado com sucesso para minha caixa de mensagens :)";
            console.log("SUCCESS!", response.status, response.text);
          },
          function (err) {
            vm.loadingStatus = false;
            vm.loadingMessage =
              "Houve algum erro no envio do seu e-mail, tente novamente, por favor. Se o erro persistir, tente as outras plataformas de contato.";
            console.log("FAILED...", err);
          }
        );
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        this.loadingStatus = true;
        this.loadingMessage = "Enviando e-mail...";
        this.$refs.form.validate();
        this.sendEmail("Send");
      }
    },
    reset() {
      this.$refs.form.reset();
      this.$refs.form.resetValidation();
    },
  },
};
</script>
